import * as React from "react";
import { Button, Grid, TextField } from "@material-ui/core";
import { primaryColor } from "shared/components/style";
// @ts-ignore
import logo from 'shared/images/Logo.png';
import axios from 'shared/components/ajax';
import { Link, navigate } from 'gatsby';
import Defer from "shared/components/ui/Defer";
import save from "shared/components/save";

const Page = () => {
  const [load, setLoading] = React.useState(false);
  /**
   * @type {React.MutableRefObject<any>}
   */
  const form = React.useRef();
  const submit = (e) => {
    e.preventDefault();
    setLoading(true);
    var payload = {
      "username": form.current.user.value,
      "password": form.current.pass.value
    };
    axios.post('/auth/login', payload).then(x => {
      save.token = x.data.token;
      localStorage.setItem('token', x.data.token);
      localStorage.setItem('user', payload.username);
      localStorage.setItem('pass', payload.password);
      navigate('/user/');
    }).catch(x => {
      alert(x.response?.data.message || x.message);
      setLoading(false);
    });
  }

  return (
    <div>
      <Defer data={!load}>
        {() => <form onSubmit={submit} ref={form}>
          <Grid container spacing={0}
            style={{ minHeight: '100vh', backgroundColor: primaryColor }}
            direction="column"
            alignItems="center"
            justifyContent="center">
            <Grid item>
              <img src={logo} width="200px" alt="" style={{ marginBottom: '1em' }} />
            </Grid>
            <Grid item>
              <TextField name="user" label="HP/NIK" variant="filled" />
            </Grid>
            <Grid item>
              <TextField name="pass" type="password" autoComplete="current-password" label="Password" variant="filled" />
            </Grid>
            <Grid item style={{margin: '0.5em'}}>
              <Button variant="contained" type="submit">
                Masuk
              </Button>
            </Grid>
            <Grid item>
              <Button component={Link} to="/user/register/">
                Belum punya akun? Daftar
              </Button>
            </Grid>
          </Grid>
        </form>
        }
      </Defer>
    </div>
  )
}

export default Page;
